@font-face {
   font-family: 'nanumsquare';
   src : url(/src/fonts/NanumSquareR.woff);
}

* {
   box-sizing: border-box;
   list-style: none;
   margin: 0;
   padding: 0;
   text-decoration: none;
}

ul {
   margin: 0;
   padding: 0;
}

.nav_bar {
   width: 100%;
   height: 90px;
   top: 0;
   z-index: 9999;
   backdrop-filter: blur(10px);
   background: hsla(0,0%,100%,.6);
   box-shadow: 0 8px 24px hsla(210,8%,62%,.2);
}


.nav_container {
   width: 100%;
   margin: 0 auto;
   max-width: 1540px;
   display: flex;
   align-items: center;
   justify-content: space-between;
   padding: 0 20px;
   box-sizing: border-box;
   height: 100%;
}

.nav_menu {
   display: flex;
   align-items: center;
}

.logo_area {
   width: 100px;
   margin-right: 20px;
   cursor: pointer;
}

.logo_area img {
   width: 100%;
}

.nav_menu li {
   padding: 0 15px;
   cursor: pointer;
   font-weight: 500;
   color: #000;
}

.nav_menu  ul {
   display: flex;
   margin: 0;
}

.nav_login ul {
   display: flex;
   align-items: center;
   margin: 0;
}

.nav_login ul li {
   margin-left: 20px;
}

.nav_hamberger {
   cursor: pointer;
   display: none;
}

.nav_basket {
   cursor: pointer;
}

.detail_main {
   display: flex;
   margin: 0 auto;
   max-width: 1500px;
   padding: 175px 0;
   box-sizing: border-box;
}

.detail_img_area {
   width: 50%;
   padding: 50px;
   background-color: #EDECDD;
}

.detail_img_area img {
   width: 100%;
}

.detail_product_text {
   width: 50%;
   padding: 100px;
   text-align: left;
   line-height: 50px;
}

.detail_product_text h1 {
   margin-bottom: 20px;
}

.detail_product_text h5 {
   font-size: 16px;
   line-height: 24px;
}


.detail_product_text p {
   font-size: 24px;
   font-weight: 900;
}


.add_cart_button {
   width: 100%;
   border: none;
   background-color: #252525;
   height: 50px;
   color: #fff;
   font-size: 20px;
}

.product_detail {
   width: 100%;
   margin-top: 30px;
   line-height: 30px;
   border-top: 2px solid #252525;
}

.product_detail li {
   border-bottom: 1px solid #ececec;
   padding: 10px 0;
}

.product_detail p {
   font-size: 16px;
   margin: 0;
}


@media screen and (max-width: 1200px){
   .detail_img_area {
      padding: 100px 0;
   }
}

@media screen and (max-width: 768px){
   .nav_hamberger {
      display: block;
   }

   .nav_menu  ul {
      display: none;
   }

   .detail_main {
      flex-wrap: wrap;
      padding: 100px 0;
   }

   .detail_img_area {
      width: 100%;
      padding: 30px;
   }

   .detail_product_text {
      width: 100%;
      padding: 30px;
   }
}